#solarSystem {
  z-index: 1;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

#threeCanvas {
  transform-origin: center;
  z-index: 50;
  pointer-events: none;
  border-radius: 0%;
  width: 100vw;
  height: 100vh;
  transition: transform 1.2s ease-in-out, border-radius 1.2s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
}

#gui-container {
  z-index: 10;
  display: block;
  position: absolute;
  top: 10px;
  right: 10px;
  background: none !important;
}
